/* noto-sans-Light - latin */
@font-face {
  font-family: 'Noto Sans';
  font-style: normal;
  font-weight: 300;
  src: local(''),
    /* Modern Browsers */ url('../fonts/NotoSans-Light.ttf') format('truetype');
}
/* noto-sans-Regular - latin */
@font-face {
  font-family: 'Noto Sans';
  font-style: normal;
  font-weight: 400;
  src: url('../fonts/noto-sans-v25-latin-regular.eot'); /* IE9 Compat Modes */
  src: local(''),
    url('../fonts/noto-sans-v25-latin-regular.eot?#iefix')
      format('embedded-opentype'),
    /* IE6-IE8 */ url('../fonts/noto-sans-v25-latin-regular.woff2')
      format('woff2'),
    /* Super Modern Browsers */ url('../fonts/noto-sans-v25-latin-regular.woff')
      format('woff'),
    /* Modern Browsers */ url('../fonts/noto-sans-v25-latin-regular.ttf')
      format('truetype'),
    /* Safari, Android, iOS */
      url('../fonts/noto-sans-v25-latin-regular.svg#NotoSans') format('svg'); /* Legacy iOS */
}
